@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.btn-text {
  font-size: 48px;
}

.btn-item {
  margin-bottom: 8px;
}

.button-panel {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 8px;
}

/* Top buttons */
.button-panel-top {
  display: flex;
}

.button-panel-top button:first-of-type {
  flex: 1 0 auto;
}

.button-panel-top button:last-of-type {
  width: 90px;
  margin-left: 8px;
}

/* Bottom buttons */
.button-panel-bottom {
  display: flex;
}

.button-panel-bottom button:nth-of-type(1) {
  flex: 1 0 auto;
  margin-right: 8px;
}

.button-panel-bottom button:nth-of-type(2) {
  margin-right: 8px;
}

.button-panel-bottom button:nth-of-type(2),
.button-panel-bottom button:nth-of-type(3) {
  width: 90px;
}

.bg-dark-blue {
  background-color: #343a40;
  height: 60px;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

